*{
  box-sizing: border-box;
}

body{
  background-color: #8e44ad;
  margin: 0;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  height: 100vh;
  
}

.display{
  background-color: #efefef;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.4);
  border-radius: 4px;
  display: flex;
  flex-wrap: wrap;
  width: 400px;
  padding: 20px;
}

.display h1{
  display: block;
  width: 100%;
  text-align: center;
}

.box {
  border: 2px solid #333;
  border-radius: 4px;
  background-color: #8e44ad;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.4);
  color: #fff;
  height: 100px;
  width: 100px;
  margin: 10px;
  font-size: 50px;
  display: flex;
  text-align: center;
  justify-content: center;
  cursor: pointer;
}

.box.active {
  opacity: 0.8;
  transform: scale(0.95);
}